html {
  scroll-behavior: smooth;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  margin: 0;
  display: block;
}

button {
  cursor: pointer;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;
}

.header {
  background-color: #20296e;
  margin: 0;
  padding-top: 15px;
  padding-bottom: 15px;
  box-shadow: 0 10px 10px -5px rgba(0, 0, 0, .3);
}

.search-form {
  height: 30px;
  justify-content: center;
  gap: 5px;
  display: flex;
}

.btn {
  width: 80px;
  box-shadow: 0px;
  border: 0;
  border-radius: 5px;
  transition-property: background-color, color;
  transition-duration: .3s;
}

.load-more.btn {
  width: 120px;
  height: 50px;
  color: #fff;
  background-color: #20296e;
  margin: 60px auto;
}

.input {
  border: 1px solid rgba(255, 255, 255, .3);
  border-radius: 5px;
  outline: 0;
  transition-property: border-color;
  transition-duration: .3s;
}

.input:focus {
  border-color: #000;
}

.btn:hover {
  color: #f0f8ff;
  background-color: #000;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

.photo-card {
  flex-basis: calc(33.3333% - 13.3333px);
}

.info {
  height: 100px;
  border: 1px solid #959596;
  border-top: 0;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  justify-content: space-evenly;
  align-items: center;
  gap: 10px;
  display: flex;
}

